@import 'global/index';
body{
  background: #f0f0f0;
}
input{
  outline: none;
}
.blank{
  display: block;
  height: 0;
  line-height: 0;
  font-size: 0;
  clear: both;
  overflow: hidden;
}
.h20{
  height: 20px;
}
.h10{
  height: 10px;
}
.h12{
  height: 12px;
}
.page{
  margin: 0 auto;
  width: 1222px;
}
body #jx3-top-navi-bar .inner-i{
  width: 1222px;
}
body #jx3-top-navi-bar{
  background: #1d2327;
}
body #jx3-top-navi-bar .t-links a{
  color: #66696b;
}
body #jx3-top-navi-bar .t-links a.hlight{
  color: #959697;
}
body #jx3-top-navi-bar .t-links a.hlight:hover{
  color: #959697;
}
body #jx3-top-navi-bar .t-links a:hover{
  background: none;
  color: #66696b;
}
